import React, { useState, useContext, createElement, useEffect } from 'react';

import Modal from '@mui/material/Modal'


// import { grey, indigo, green } from '@mui/material/colors'

import Box from '@mui/material/Box'




import { AuthContext } from "../../context/general"




import  {STAGE_LOGIN, initialState} from './general'
import workflow  from './logic'




const  LoginModal = () => {

 
    const { authState, authActions } = useContext(AuthContext)

    const [localState, setLocalState] = useState(initialState(authState))

    useEffect (()=>{
       
        setLocalState(initialState(authState))

    }, [authState.showLoginModal])

  
    


  
    
    // SETUP:
        const open = authState.showLoginModal
    // END

    
    

    // function that will be invoked when the workflow is finished

     // ADD BACK FOR DEBUG
    //const _currentProcess = workflow.find(stage=>stage.name === 'PAGE_CONFIRMATIONA')
    
   
    const _currentProcess = workflow.find(stage=>stage.name === localState[STAGE_LOGIN])
    
   
    const currentProcess = {
        ..._currentProcess,
        closeHandler: () => {setLocalState(initialState(authState))
            authActions.toggleLoginModal()},
        setAuthData: authActions.setUserData,
        setCookies: authActions.setCookies
    }
  
  
  
  if (!currentProcess.component)
    return (null)



   
  
    return (<div>
        <Modal
        open={Boolean(open)}
        onClose={() => { }}>
            <div>
            
            <WorkflowContainer>     
            {createElement(currentProcess.component, 
            {
                'currentProcess' : currentProcess,
                'localState': localState,
                'setLocalState': setLocalState,
                'isLogged' : authState.user?.id ? true : false,
                'showLoginModal': authState.showLoginModal
            }
            )}
            </WorkflowContainer>
            </div>
           
        </Modal>
    </div>)
    }

    

    const WorkflowContainer = ({children}) =>{

      
        return(
            <Box display="flex" flexDirection="row"  justifyContent="center" alignItems="center" width="100vw" height="100vh" bgcolor="#FFFFFFA0">
            {children}
           
        </Box>)
    }


export default LoginModal




  







