// STAGES

const PAGE_HOMEPAGE = 'PAGE_HOMEPAGE'
const PAGE_HOMEPAGE_NOTLOGGED = 'PAGE_HOMEPAGE_NOTLOGGED'
const PAGE_0 = 'PAGE_0'
const PAGE_2 = 'PAGE_2'
const PAGE_3 = 'PAGE_3'
const PAGE_WITHACCOUNT = 'PAGE_WITHACCOUNT'
const PAGE_WITHNOACCOUNT = 'PAGE_WITHNOACCOUNT'
const PAGE_PAIEMENTNOACCOUNT = 'PAGE_PAIEMENTNOACCOUNT'

const PAGE_NEWACCOUNT = 'PAGE_NEWACCOUNT'

const PAGE_NOUVEAUMDP = "PAGE_NOUVEAUMDP"

// MODAL LOCAL STATE VARIABLE NAMEs




// definition of workflow existing pages
const pages = {
    attach: (name, item) => {
        const page = pages.scope.find(page => page.name === name)
        Object.keys(item).forEach(key => page[key] = item[key])

    },
    scope: [
        {
            name: PAGE_HOMEPAGE,
            displayComponent: SubscriptionHomePage
        },

        {
            name: PAGE_HOMEPAGE_NOTLOGGED,
            displayComponent: SubscriptionHomePage
        },

        {
            name: PAGE_WITHACCOUNT,
            displayComponent: WithAccount
        },
        {
            name: PAGE_WITHNOACCOUNT,
            displayComponent: WithNoAccount
        },

        {
            name: PAGE_PAIEMENTNOACCOUNT,
            displayComponent: PaiementNoAccount
        },
        {
            name: PAGE_LOGIN,
            displayComponent: Login
        },
        {
            name: PAGE_NEWACCOUNT,
            displayComponent: NewAccount
        },
        {
            name: PAGE_CONFIRMATION,
            displayComponent: EmailConfirmation
        },
        {
            name: PAGE_NOUVEAUMDP,
            displayComponent: NouveauMdp
        }


    ]
}

// DEFINING PROCESSES LOGIC
// availables prosp for functions are:
// localState: availables "internal states of the navigation handler": EMAIL, PASSWORD, DIGITS, ERROR_MESSAGE, STAGE
// setLocalState: setter for internal states
// currentProcess: give access to routines that will allow to update the global state: setAuthData

// helper for improved clarity of code
const localStateAPI = (props) => ({
    moveTo: (page, stage) => {


        props.setLocalState({ ...props.localState, [ERROR_MESSAGE]: '', [STAGE_LOGIN]: page + stage })
    },
    increaseCount: () => props.setLocalState({ ...props.localState, [COUNT]: props.localState + 1 }),
    displayErrorMessage: (message) => props.setLocalState({ ...props.localState, [ERROR_MESSAGE]: message })


})


// Description of workflow logic that will be attached to the workflow

const workflowLogicHomePage = {
    onClickMainButton: (props) => {

        if (props.isLogged)
            localStateAPI(props).moveTo(PAGE_WITHACCOUNT, 'A')
        else
            localStateAPI(props).moveTo(PAGE_LOGIN, 'A')

    },
    onClickSecondButton: (props) => {

        localStateAPI(props).moveTo(PAGE_WITHNOACCOUNT, 'A')
    }

}

const workflowLogic0A = {
    onClickMainButton: (props) => {
        const { localState } = props
        if (testEmail(localState[EMAIL])) {
            const callback = {
                available: () => localStateAPI(props).displayErrorMessage("Il n'y aucun compte Pekoia associé à cet email"),
                notConfirmed: () => localStateAPI(props).moveTo(PAGE_3, 'A'),
                notAvailable: () => localStateAPI(props).moveTo(PAGE_0, 'B')

            }
            testIfEmailExists(localState[EMAIL], callback)
        }
        else localStateAPI(props).displayErrorMessage('format email incorrect')


        //localStateAPI(props).moveTo(STAGE_0B)
    },
    onClickSecondButton: (props) => {
        const { localState } = props

        if (testEmail(localState[EMAIL])) {
            const callback = {
                available: () => localStateAPI(props).moveTo(PAGE_2, 'A'),
                notConfirmed: () => console.log('B'),
                notAvailable: () => localStateAPI(props).displayErrorMessage('Cet email a déja été utilisé.')

            }
            testIfEmailExists(localState[EMAIL], callback)

        }
        //'format email incorrect'
        else localStateAPI(props).displayErrorMessage('format email incorrect')
    }
}





