const workflowLogicLogin = {
    onClickMainButton: (props) => {

     



        const { localState, setLocalState, currentProcess } = props


        if (props.showLoginModal === 2) {

            localStateAPI(props).moveTo(PAGE_NEWACCOUNT, 'A')

            return null
        }

        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            password: localState.PASSWORD,
            callback: (data) => {
                currentProcess.setAuthData(data)

                fetchUserData(data, currentProcess.setAuthData)
                currentProcess.closeHandler()
            },
            onError: (err) => {

                const error1 = "NotAuthorizedException"
                const error2 = "UserNotFoundException"
                let errorMessage = 'connexion impossible'

                if (err.toString().includes(error1))
                    errorMessage = 'login ou mot de passe incorrect.'
                if (err.toString().includes(error2))
                    errorMessage = 'login ou mot de passe incorrect.'
                if (err.code?.includes('NotConfirmed'))
                    localStateAPI(props).moveTo(PAGE_CONFIRMATION, 'B')
                else {
                    setLocalState({ ...localState, [ERROR_MESSAGE]: errorMessage })
                }

            }
        }).signIn()

    },
    onClickSecondButton: (props) => localStateAPI(props).moveTo(PAGE_NEWACCOUNT, 'A'),
    onClickThirdButton: (props) => {
        console.log('@MOVE-TO-B')
        localStateAPI(props).moveTo(PAGE_NOUVEAUMDP, 'A')
    }
}




const workflowLogicNewAccount = {


    onClickMainButton: (props) => {





        const { localState } = props
        localStateAPI(props).displayErrorMessage('')

        props.authActions.setConsentData(true)


        AuthAPI({
            email: localState.EMAIL,
            password: localState.PASSWORD,


            callback: () => localStateAPI(props).moveTo(PAGE_CONFIRMATION, 'B'),
            onError: (err) => {

                console.log('*** ERROR: A ****')
                console.log(err)
                let messageError = 'problème de connexion'
                const error1 = 'UsernameExistsException:'
                const error2 = 'CodeDeliveryFailureException'
                if (err.toString().includes(error1))
                    messageError = 'ce login existe déja.'
                if (err.toString().includes(error2))
                    messageError = "problème d'envoi de mail.\nVeuillez nous contacter à l'adresse suivante: contact@pekoia.com"




                localStateAPI(props).displayErrorMessage(messageError)
            }
        }).signUp()


        // .. ask for a new password
        //setLocalState({...localState, [STAGE]: STAGE_3A})
    },
    onClickSecondButton: (props) => localStateAPI(props).moveTo(PAGE_LOGIN, 'A'),
    onClickThirdButton: (props) => {

    }
}

const workflowLogicNouveauMdp = {
    onClickMainButton: (props) => {

        console.log('@FORGOT-A')

        const { localState, setLocalState } = props
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                console.log('@CONF-B')
                localStateAPI(props).moveTo(PAGE_CONFIRMATION, 'A')
            },
            onError: (err) => {

                let errorMessage = '-- problème de connexion --'
                console.log('@ERROR')
                console.log(err?.toString())
                const error1 = 'LimitExceededException'
                const error2 = 'Cannot reset password for the user as there is no registered/verified email or phone_number'
                const error3 = 'UserNotFoundException:'
                if (err?.toString().includes(error1))
                    errorMessage = 'Nombre de tentatives excessif. Essayez à nouveau plus tard.'
                if (err?.toString().includes(error2))
                    errorMessage = 'Impossible de réinitialiser le mot de passe (compte non confirmé). Confirmez votre compte en vous connectant.'
                if (err?.toString().includes(error3))
                    errorMessage = 'erreur: login inexistant.'
                setLocalState({ ...localState, [ERROR_MESSAGE]: errorMessage })
            }
        }).forgot()

    },

    onClickButton: (props) => {
        const { localState } = props
        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                localStateAPI(props).displayErrorMessage('un nouveau code vous a été envoyé.')
            },
            onError: (err) => {
                console.log('@ERROR - R')
                console.log(err)
                localStateAPI(props).displayErrorMessage('problème de connexion')
            }
        }).resendSignUp()
    }
}

const workflowLoginEmailConfirmation = {
    onClickMainButton: (props) => {

        console.log('@WKF-MAIN')

        localStateAPI(props).displayErrorMessage('')
        const { localState, setLocalState, currentProcess } = props

        localStateAPI(props).displayErrorMessage('')

        AuthAPI({
            email: localState.EMAIL,
            //password: localState.PASSWORD,
            digits: localState.DIGITS.join(''),
            callback: () => {

                AuthAPI({
                    email: localState.EMAIL,
                    password: localState.PASSWORD,
                    callback: (data) => {
                        currentProcess.setAuthData(data)
                        currentProcess.closeHandler()
                        fetchUserData(data, currentProcess.setAuthData, 0, localState.CONSENT)
                    },
                    onError: () => setLocalState({ ...localState, [ERROR_MESSAGE]: 'problème de connexion-C' })
                }).signIn()


            },
            onError: (err) => {

                console.log('*** ERROR: B ****')

                const error = 'CodeMismatchException:'
                let message = 'problème de connexion'

                if (err.toString().includes(error))
                    message = 'code de vérification faux.'

                console.log('@ERROR-D')
                console.log(err)

                setLocalState({ ...localState, [ERROR_MESSAGE]: message })
            }
        }).confirmSignUp()

    }
    ,
    onClickButton: (props) => {
        const { localState } = props
        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                localStateAPI(props).displayErrorMessage('un nouveau code vous a été envoyé.')
            },
            onError: (err) => {
                console.log('@ERROR - R')
                console.log(err)
                localStateAPI(props).displayErrorMessage('problème de connexion')
            }
        }).resendSignUp()
    }
}

